.swiper1 {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 50px;
}

.swiper-slide1 {
  background-position: center;
  background-size: cover;
  width: 200px;
}

.swiper-slide1 div {
  display: block;
  width: 100%;
}

.swiper {
  width: 100%;
  height: 100%;
  background-color: white;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
}

.swiper-slide img {
  display: block;
  /* width: 60%;
  height: 100%; */
  object-fit: cover;
}

.tabbar-section {
  border-top: 1px dashed var(--app-color-gray-lightest);
  width: 100%;
  padding-left: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  background-color: white;
}
.tabbar-icon {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.navbar-section {
  width: 100%;
  padding-left: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  background-color: white;
}

.navbar-title a {
  text-decoration: none;
  color: #003C71;

}

.navbar-title {
  font-family: var(--app-text-main-font);
  font-family: 'Montserrat';
  font-weight: normal;
  margin-left: 100px;

}

.navbar-sign {
  color: #54de54;
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
  font-weight: bold;
}

.navbar-items {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 32px;
  font-family: 'Montserrat', sans-serif;
  width: 50%;
}

.navbar-items>li {
  list-style-type: none;
}

.navbar-links {
  text-decoration: none;
  color: black;
  font-size: 18px;
  letter-spacing: .8px;
}

.navbar-links:hover {
  color: #0cc2ea;
}

.navbar-btn {
  padding: 14px 20px;
  color: white;
  border: 1px solid transparent;
  border-radius: 28px;
  outline: transparent;
  background-color: #1A8EFD;
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: .8px;
  cursor: pointer;
  transition: all .4s ease;
}

.navbar-btn:hover {
  color: #1A8EFD;
  background-color: white;
  border: 1px solid #1A8EFD;
}

/* Hamburger Icon */
.mobile-nav {
  display: block;
}

.hamb-icon {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

/* Mobile Navbar */
.mobile-navbar {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: -100%;
  background-color: white;
  z-index: 20;
  justify-content: center;
  text-align: center;
  align-items: center;
  transition: left .5s ease-in-out;
}

.mobile-navbar-close {
  position: absolute;
  top: 28px;
  right: 28px;
}

.mobile-navbar-close .hamb-icon:hover {
  color: rgb(255, 22, 22);
}

.open-nav {
  left: 0;
}

.mobile-navbar-links {
  width: 100%;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  text-align: left;
}

.mobile-navbar-links li a {
  text-decoration: none;
  color: black;
  font-family: var(--app-text-mobile-font), serif;
  font-weight: light;
  font-size: 18px;
  transition: color .3s ease-in-out;
}

.mobile-navbar-links li a:hover {
  color: #0cc2ea;
  cursor: pointer;
}

/* Responsive */
@media screen and (max-width: 900px) {

  .navbar-btn,
  .navbar-items {
    display: block;
  }

  .mobile-nav {
    display: block;
  }
}