.host {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  justify-content: center;
  align-items: center;
  padding: 10px;
}


.dashboardPanel {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 10px 0px 100px 0px;
  justify-content: center;
  align-items: center;
  width: inherit;
}

.headerContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 30px
}

.title {
  font-family: var(--app-text-main-font);
  font-size: 1.5rem;
  line-height: 1.5rem;
  color: var(--app-color-gray-dark);
}

.modalTitle {
  font-family: var(--app-text-main-font);
  font-size: 2rem;
  color: var(--app-color-gray-dark);
  margin-top: 5px;
  margin-bottom: 5px;
  text-align: center
}

.singleButton {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  outline: none;
  cursor: pointer;
  font-family: var(--app-text-main-font);
  padding: 10px;
  margin: 10px 10px 10px 0px;
  height: 40px;
  overflow: hidden;
  border: 1px solid var(--app-color-gray-lighter);
  background-color: var(--app-color-white-lightest);
  color: var(--app-color-gray-dark);
  stroke: var(--app-color-gray-dark);
  width: 120px;
}

.fieldRowContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  margin-bottom: 10px;
  height: 32px;
  flex: 1 1;
}

.fieldTypeInput {
  ::placeholder {
    font-family: var(--app-text-main-font);
    font-size: 1rem;
    color: var(--app-color-red-error);
  }

  outline: none;
  border: none;
  padding-left: 10px;
  border: 0px;
  min-width: 350px;
  width: 100%;
  height: 37px;
  font-family: var(--app-text-main-font);
  font-size: 1rem;
  color: var(--app-color-gray-dark);
}

.dashboardCard {
  min-width: 200px;
  min-height: 160px;
  width: 100%;
  cursor: pointer;
  border-radius: 5px;
  font-weight: 100;
  font-size: 18px;
  text-align: left;
  padding-left: 20px;
  color: white;
  background-color: rgb(46, 149, 84);

  p {
    text-align: center;
    font-size: 22px;
    color: var(--app-color-gray-dark);
    color: white;
    min-height: 40px;
  }


  &[app-color-variant="0"] {
    background-color: orange;
  }

  &[app-color-variant="1"] {
    background-color: rgb(46, 149, 84);
  }

  &[app-color-variant="2"] {
    background-color: rgb(255, 115, 0);
  }

  &[app-color-variant="3"] {
    background-color: rgb(112, 37, 217);
  }

  &[app-color-variant="4"] {
    background-color: var(--app-color-blue);
  }
}

.componentCard {
  min-width: 200px;
  min-height: 160px;
  width: 100%;
  cursor: pointer;
  border-radius: 5px;
  font-weight: 100;
  font-family: var(--app-text-mobile-font);
  font-size: 18px;
  text-align: left;
  padding-left: 20px;
  color: white;
  background-color: rgb(46, 149, 84);

  p {
    text-align: left;
    font-size: 22px;
    color: var(--app-color-gray-dark);
    color: white;
    min-height: 40px;
  }

  &[app-color-variant="0"] {
    background-color: rgb(46, 149, 84);
  }

  &[app-color-variant="1"] {
    background-color: rgb(112, 37, 217);
  }

  &[app-color-variant="2"] {
    background-color: var(--app-color-blue);
  }

  &[app-color-variant="3"] {
    background-color: orange;
  }

  &[app-color-variant="4"] {
    background-color: rgb(255, 115, 0);
  }
}

.imageCard {
  min-width: 200px;
  max-height: 200px;
  width: 100%;
  cursor: pointer;
  border-radius: 5px;
  //background-color: var(--app-color-gray-lightest);

  img {
    padding: 6px;
    width: inherit;
    height: inherit;
    
  }

}

.newTaskButton {
  //background-color: #EC2027;
}

.searchContainer {
  width: 80%;
  display: flex;
  margin: 20px 0 0 0;
  border-bottom: 1px solid var(--app-color-gray-light);

}

.searchInput {
  width: inherit;
  border: 0;
  padding: 0 0 0 10px;
  font-size: 20px;
  font-family: var(--app-text-main-font);

}

.ongoingtx {
  width: inherit;
  border: 0;
  padding: 20px 0 0 10px;
  font-size: 16px;
  font-family: var(--app-text-main-font);
  color: var(--app-color-gray);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
}

.dropdownSelectComponent {
  border: none;
  width: 20%;
  margin-right: 20px;
}

.dropdownSelectMenuOverlayList {
  right: 0px;
  left: -30px;
  width: calc(100% + 30px);
  top: 76%;
  border-radius: 8px;
  //box-shadow: 0px 0px 30px #0000001a;
}

.dropdownSelectMenuOverlayListItem {
  color: var(--app-color-gray-dark);
  padding: 5px 0;
}

.dropdownInput {
  color: var(--app-color-gray-dark);
  font-size: 20px;
}

.dropdownClearControlsClassName {
  padding: 0 10px;
  color: var(--app-color-gray-darker);
}

.listContainer{
  padding: 0 5px 0 5px;
}

.dropdownListContainer {
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 4px;
  min-height: 200px;
  width: 100%;
  padding: 12px;

  &[app-variant="error"] {
    border: 1px solid var(--app-color-red-error);
  }
}

.dropDownButtonContainer {
  width: 100%;
  border: 1px dashed var(--app-color-gray-light);
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 12px;
}

.searchResults {
  margin-top: 50px;
  min-height: 160px;
  height: auto;
  min-width: 100%;
  max-width: 100%;
  border: 0;
  font-family: var(--app-text-main-font);
  font-size: 20px;
  background-color: white;
}

.pageContainer {
  display: flex;
  align-items: center;
  max-height: 80%;
  margin: 0;

  width: 10px p {
    height: 20px;
    font-family: var(--app-text-main-font);
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0;
    line-height: 20px;
  }
}

.emulatedFlexGap>* {
  //margin: 12px 0 0 12px;
  margin-left: 10px;
}

.emulatedFlexGap {
  padding-top: 10px;
  padding-bottom: 15px;
  display: inline-flex;
  flex-wrap: wrap;
  align-content: center;

  align-self: center;
}

.popup {

  flex-direction: column;
  padding: 30px;
  justify-content: flex-start;
  align-content: center;
  top: -100%;
  display: none;
  border-radius: 20px;

  &[app-variant-opened="true"] {
    display: flex;
    opacity: 1;
    top: 80%;
    z-index: 1;
    transition: all 1s ease-out;
  }

  filter: none;
  background-color: var(--app-color-white-lightest);
  position: fixed;
  left: 0;
  height: auto;

}

.profilepopup {
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  top: -100%;
  display: none;
  border-radius: 20px;

  &[app-variant-opened="true"] {
    display: flex;
    opacity: 1;
    top: 20%;
    z-index: 1;
    transition: all 1s ease-out;
  }

  filter: none;
  background-color: var(--app-color-white-lightest);
  position: fixed;
  left: 0;
  height: auto;

}

.userCardContainerTop {
  display: flex;

}

.userCardInnerContainer {
  height: 30px;
  width: 30px;
  background-color: var(--app-color-gray-lighter);
  border-radius: 50%;
}

.avatarUrlImg {
  height: 30px;
  width: 30px;
  border-radius: 50%;
}

.center {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: top;
}

.moduleTitle {
  color: var(--app-color-gray-dark);
  font-family: var(--app-text-mobile-font), serif;
  font-size: 18px;
  font-weight: light;
  text-align: center;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.swiperslide {
  //width: 600px;
  //height: 200px;

}

.filtersContainer {
  margin-top: 2px;
  width: inherit;
  margin-bottom: 10px;
  border: 1px dashed var(--app-color-gray-lighter);
  padding: 20px 10px 20px 10px;
  height: auto;
}

.wallet {
  margin-top: 20px;
  width: inherit;
  margin-bottom: 10px;
  border-radius: 5px;
  padding: 20px 10px 20px 15px;
  background-color: rgb(46, 149, 84);
  height: auto;
}

.userPanelRows {
  display: flex;
  flex-direction: column;

  border-radius: 7px;
  padding: 0px 5px 00px 5px;
}

.actionsContainer {
  display: flex;
  justify-content: space-between;
  width: inherit;
  font-size: 10px;
}

.appsTitle {
  padding-left: 10px;
  margin-top: 10px;
  color: var(--app-color-gray-dark);
  font-family: var(--app-text-main-font), serif;
  font-size: 18px;
  font-weight: bold;
}

.stepTitle {
  padding-left: 20px;
  margin-top: 10px;
  color: var(--app-color-gray-dark);
  font-family: var(--app-text-main-font), serif;
  font-size: 24px;
  text-justify: left;
  width: 80%;
}